<template>
    <div class="inner-class">
        <h2>{{addVideoHeader}}</h2>
        <hr>
        <form>
            <div class="form-group">
                <label for="nameInput">{{nameText}}</label>
                <input type="text" class="form-control" id="nameInput" placeholder="...">
            </div>
            <div class="form-group">
                <label for="descriptionInput">{{descriptionText}}</label>
                <input type="text" class="form-control" id="descriptionInput" placeholder="...">
            </div>
            <div class="form-group">
                <label for="fileInput">{{fileInputText}}</label>
                <input type="file" id="fileInput">
            </div>
            <button type="submit" class="btn btn-default">{{sendVideoText}}</button>
        </form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            addVideoHeader: " ",
            nameText: "  :",
            descriptionText: "  :",
            fileInputText: " :",
            sendVideoText: ""
        }
    }
}
</script>




